import { Component, OnInit } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { StudentService } from '../student.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit{
  currentFacilitatorId: string;
  currentFacilitatorName : string;
  orgName:string;
  //this
  anyrole:boolean=false;
  menuItems = ['dashboard', 'students', 'add students','recordings', 'report']
  // menuItems = ['dashboard', 'students', 'stories', 'facilitators','recordings']

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );
  
  isHandSetPlayerStyle = {
    left : "0",
    width : "100%"
  }

  isNotHandSetPlayerStyle = {
    left : "15.75%",
    width : "84.24%"
  }

  constructor(
    private breakpointObserver: BreakpointObserver,
    private router: Router,
    ) {
      this.router.onSameUrlNavigation = 'reload';
    }
  
  ngOnInit(): void {

    this.currentFacilitatorName = "Joshua Fernandes"
    this.orgName = "IIT Bombay";
    
  }
  showlable:boolean=false;
  
  facilitatorSchool:any[];

  selectedSchool:string;
  refreshId(schoolId:string){
    console.log(`will pass : ${schoolId}`)
    console.log(`url pass : ${this.router.url}`)
  }
  
}
